function useGraphState() {
  const [graph_nodes, setNodes] = useState(nodes);
  const [graph_edges, setEdges] = useState(edges);
  const [selected, setSelected] = useState(null);
  // const [options, setOptions] = useState({
  //   show_projects: true
  // });
  const [show_projects, setShowProjects] = useState(false);
  const [show_effects, setShowEffects] = useState(false);
  const [show_objects, setShowObjects] = useState(false);

  return {
    nodes: graph_nodes,
    edges: graph_edges,
    selected: selected,
    show_projects: show_projects,
    show_effects: show_effects,
    show_objects: show_objects,
    setNodes,
    setEdges,
    setSelected,
    setShowProjects,
    setShowEffects,
    setShowObjects
  };
}

// Convert nodes to format usable by react-flow
function ConvertNode(node) {
  return {
    id: node["node"]["dataName"],
    type: node["type"],
    data: node,
    position: { x: 0, y: 0 }
  };
}

// Convert edges to format usable by react-flow
function ConvertEdge(edge) {
  return {
    id: edge["from"] + "_" + edge["to"] + "_" + edge["type"],
    source: edge["from"],
    target: edge["to"],
    type: edge["type"],
    data: edge,
    // animated: edge["type"] === "STEP" ? false : true
    animated: false
  };
}

console.log("Loaded " + edges.length + " edges.");

function GetLayout(state, nodes, edges) {
  // Create a new directed graph
  var g = new dagre.graphlib.Graph();
  g.setGraph({});
  g.setDefaultEdgeLabel(function () { return {}; });

  // Set some layout properties
  g.graph().rankDir = 'TD';
  g.graph().ranker = 'longest-path';
  // g.graph().ranker = 'tight-tree';
  g.graph().nodesep = 50;
  g.graph().ranksep = 50;
  console.log(nodes);

  // Add nodes to the graph
  let node_id_set = new Set();
  nodes.forEach(node => {
    // console.log("Adding node '" + node["id"] + "' with label '" + node.data.label + "'");
    let height = HEIGHT;
    if (state.show_projects) {
      // Find the number of children
      // height += PROJECT_HEIGHT * tech_projects[node.data.node.dataName]?.length;
      if (tech_projects[node.data.node.dataName]) {
        height += PROJECT_HEIGHT * tech_projects[node.data.node.dataName].length;
      }
    }
    g.setNode(node.id, {
      label: node.data.label,
      width: WIDTH,
      height: height
      // width: HEIGHT,
      // height: WIDTH
    });
    node_id_set.add(node.id);
  });

  // Add edges to the graph
  edges.forEach(edge => {
    // Skip the edge if the source or target isn't in the graph
    if (!node_id_set.has(edge.source) || !node_id_set.has(edge.target)) {
      return;
    }

    // console.log("Adding edge between " + edge.source + " and " + edge.target);
    g.setEdge(edge["source"], edge["target"]);
  });

  console.log(g);

  // Calculate the layout
  dagre.layout(g);

  // Set the position of each node, in place
  nodes.forEach(node => {
    var nodeWithPosition = g.node(node.id);
    node.position = { x: nodeWithPosition.x, y: nodeWithPosition.y };
    // node.position = { x: nodeWithPosition.y, y: nodeWithPosition.x };
  });
}

function GenerateDot(nodes, edges) {
  // Generate and print graphviz dot
  let dot = "digraph G {\n";
  let node_set = new Set();
  nodes.forEach(node => {
    console.log(node);
    let label = node.data.node.friendlyName;
    // Replace '-' with ' '
    label = label.replace('-', ' ');
    dot += "  " + node.id + " [label=\"" + label + "\"];\n";
    node_set.add(node.id);
  });
  edges.forEach(edge => {
    // Skip the edge if the source or target isn't in the graph
    if (!node_set.has(edge.source) || !node_set.has(edge.target)) {
      return;
    }
    dot += "  " + edge.source + " -> " + edge.target + ";\n";
  });

  dot += "}\n";
  console.log(dot);
}

function FindPredecessors(node, edges) {
  // Recursively find nodes that are predecessors of the given node
  let predecessors = [];
  let nodeSet = [node];
  while (nodeSet.length > 0) {
    let currentNode = nodeSet.pop();
    edges.forEach(edge => {
      if (edge.target === currentNode) {
        predecessors.push(edge.source);
        nodeSet.push(edge.source);
      }
    });
  }
  return predecessors;
}

function FindSuccessors(node, edges) {
  // Recursively find nodes that are successors of the given node
  let successors = [];
  let nodeSet = [node];
  while (nodeSet.length > 0) {
    let currentNode = nodeSet.pop();
    edges.forEach(edge => {
      if (edge.source === currentNode) {
        successors.push(edge.target);
        nodeSet.push(edge.target);
      }
    });
  }
  return successors;
}

function FindChain(node, edges) {
  // Recursively find nodes that are predecessors and successors of the given node
  let predecessors = FindPredecessors(node, edges);
  let successors = FindSuccessors(node, edges);

  let chain = [...predecessors, node, ...successors];
  console.log("Chain of " + node + ": " + chain);
  return new Set(chain);
}

function ApplyFilters(nodes, filters) {
  // Filter any nodes which don't match the given filters
  console.log(filters);

  if (filters.category.length === 0) {
    return nodes;
  }

  // Filter by categories
  let filtered_nodes = nodes.filter(node => {
    return filters.category.includes(node.data.node.techCategory);
  });

  console.log("Filtered from " + nodes.length + " to " + filtered_nodes.length + " nodes.");
  return filtered_nodes;
}

