function GetIDMap(nodes) {
  let id_to_node = {};
  nodes.forEach(node => {
    id_to_node[node.data.node.dataName] = node;
  });
  return id_to_node;
}

let tech_id_to_node = GetIDMap(tech_nodes);
let project_id_to_node = GetIDMap(project_nodes);
let effect_id_to_node = GetIDMap(effect_nodes);
let object_id_to_node = GetIDMap(object_nodes);

// Convert edges
