// Get projects for each tech
const tech_projects = {};
const project_prereqs = {};
Array.from(project_nodes).forEach(project => {
  project.data.node.prereqs.forEach(prereq => {
    if (prereq === "") {
      return;
    }
    // Register project with the tech
    if (tech_projects[prereq] === undefined) {
      tech_projects[prereq] = [];
    }
    tech_projects[prereq].push(project.id);

    // Register tech requirement with the project
    if (project_prereqs[project.id] === undefined) {
      project_prereqs[project.id] = [];
    }
    project_prereqs[project.id].push(prereq);
  });
});
console.log(tech_projects);

// Find effects of each tech
const tech_effects = {};
Array.from(tech_nodes).forEach(tech => {
  tech.data.node.effects.forEach(effect => {
    if (effect === "") {
      return;
    }

    // Register effect with the tech
    if (tech_effects[tech.id] === undefined) {
      tech_effects[tech.id] = [];
    }
    tech_effects[tech.id].push(effect);
  });
});

