function GetEdgeOptions(nodes, edge) {
  // Find the source and target nodes
  let source_node = nodes.find(node => node.id === edge.source);
  let target_node = nodes.find(node => node.id === edge.target);

  if (source_node === undefined || target_node === undefined) {
    return {};
  }

  // Get the colors of each node
  // let source_type = source_node.data.node.techCategory;
  let target_type = target_node.data.node.techCategory;

  // Get the color of the edge
  // let source_color = CATEGORIES[source_type].border;
  let target_color = CATEGORIES[target_type].border;

  // Darken the color a bit
  target_color = chroma.mix(target_color, 'black', 0.4);
  // let color = chroma.mix(source_color, target_color, 0.5, 'rgb');
  return {
    animated: false,
    style: {
      opacity: 1,
      strokeWidth: 2,
      stroke: target_color,
    }
  };
}

