function ProjectWidget(tech) {
  // This is a child project
  let data = project_nodes.find(node => node.id === tech);
  // console.log(data);

  // Get the prereqs for this project
  let prereqs = project_prereqs[tech];
  // Sort
  prereqs.sort((a, b) => {
    return a.localeCompare(b);
  });
  // console.log(prereqs);

  let prereq_techs = prereqs.map(prereq => {
    if (prereq.toLowerCase().startsWith("project_")) {
      return project_id_to_node[prereq];
    }
    return tech_id_to_node[prereq];
  });

  let has_multiple_prereqs = prereq_techs.length > 1;

  let icon = (
    <ListItemIcon sx={{
      minWidth: 0,
      marginLeft: -1,
      marginRight: 1,
    }}>
      <img
        src={CATEGORIES[data.data.node.techCategory].icon}
        alt="Project"
        style={{
          width: 24,
          height: 24,
          padding: 0,
          transform: "translate(3px, 3px)",
        }}
      />
    </ListItemIcon>
  );

  if (has_multiple_prereqs) {
    let reqs = "Requires " + prereq_techs.map(tech => tech.data.node.friendlyName).join(", ");
    // Darken
    icon = (
      <Tooltip title={reqs} placement="right">
        <Badge badgeContent={prereqs.length}
          color="success"
          overlap="circular"
          variant="dot"
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          sx={{
            transform: "translate(-3px, -3px)",
          }}
        >
          {icon}
        </Badge>
      </Tooltip>
    );
  }
  return (
    <ListItem sx={{
      marginTop: -1,
      marginBottom: -1
    }}>
      { /* Tech icon from category */}
      {icon}
      <ListItemText primary={data.data.node.friendlyName} />
    </ListItem>
  )
}

function EffectWidget(tech) {
  // This is a child project
  let data = effect_nodes.find(node => node.id === tech);
  // console.log(data);

  return (
    <ListItem sx={{
      marginTop: -1,
      marginBottom: -1
    }}>
      <ListItemText primary={GetLocalisedString(data.data.node.dataName).substring(1)} />
    </ListItem>
  )
}

function ShowChildList(children) {
  // Sort by tech name
  if (children === undefined) {
    return null;
  }

  children.sort((a, b) => {
    return a.localeCompare(b);
  });

  // Create MUI list
  let list = children.map(child => {
    let name = child.toLowerCase();

    if (name.startsWith("project_")) {
      return ProjectWidget(child);
    } else if (name.startsWith("effect_")) {
      return EffectWidget(child);
    } else {
      return null;
    }
  });

  return (
    <List sx={{
      width: '100%',
      maxWidth: 360,
      bgcolor: 'grey.400',
      borderRadius: 2,
      // wrap text
      // wordWrap: 'break-word',
      overflowWrap: 'break-word'
    }}>
      {list}
    </List>
  )
}

function TechChildren(data) {
  // Get all the techs which have this tech as a prerequisite,
  // and create a MUI stack holding them
  // console.log(data);
  let project_children = tech_projects[data.node.dataName];
  let effect_children = tech_effects[data.node.dataName];

  return (
    <Stack direction="column" spacing={1}>
      {ShowChildList(project_children)}
      {ShowChildList(effect_children)}
    </Stack>
  );
}


// 'tech' node type
function CreateReactFlowNode(state) {
  console.log("RF Create State:", state);
  function ReactFlowNode({ data }) {
    let node_selected = (state.selected && state.selected === data.id);
    return (
      <>
        <Handle type="target" position={Position.Top} />
        <Box sx={{
          // maxWidth: WIDTH,
          // maxHeight: HEIGHT,
          width: WIDTH,
          // height: HEIGHT,
          // height: 120,
          padding: 1,
          bgcolor: 'grey.500',
          borderColor: CATEGORIES[data.node.techCategory].border,
          borderStyle: 'solid',
          borderWidth: 3,
          borderRadius: 2,
          textAlign: 'left',
          color: node_selected ? 'white' : 'black',
        }}>
          { /* Icon and name */}
          <Stack direction="row" spacing={1} justifyContent="center" alignItems="right">
            <Tooltip title={data.node.techCategory}>
              <img
                src={CATEGORIES[data.node.techCategory].icon}
                alt={data.node.friendlyName}
                // width="32" height="32"
                style={{
                  width: "32px",
                  height: "32px",
                }}
              />
            </Tooltip>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              {data.node.friendlyName}
            </Typography>
          </Stack>
          {state.show_projects && TechChildren(data)}
        </Box>
        <Handle type="source" position={Position.Bottom} />
      </>
    );
  }

  return ReactFlowNode;
}


function useFilterButtons(state, onChange, onReset) {
  const refresh = useRefresh();

  const [filters, setFilters] = useState({
    tech: true,
    project: true,
    effect: true,
    object: true,
    search: "",
    category: []
  });

  let button = (
    <div>
      <Button
        variant="contained"
        onClick={() => {
          onReset(setFilters);
        }}
        sx={{ zIndex: 10000, position: 'absolute', top: 66, left: 2 }}
      >
        Reset
      </Button>

      <Button
        variant={state.show_projects ? "contained" : "outlined"}
        onClick={() => {
          console.log("Setting show_projects to", !state.show_projects);
          state.setShowProjects(!state.show_projects);
          refresh();
        }}
        sx={{ zIndex: 10000, position: 'absolute', top: 66, left: 84 }}
      >
        Projects
      </Button>

      <ToggleButtonGroup
        value={filters.category}
        onChange={(event, newFilters) => {
          onChange();
          setFilters({ ...filters, category: newFilters });
        }}
        aria-label="category filters"
        sx={{
          position: 'absolute',
          top: 2,
          left: 2,
          zIndex: 1,
          padding: 1,
          bgcolor: 'grey.600',
          borderColor: 'grey.600',
          borderStyle: 'solid',
          borderWidth: 3,
          borderRadius: 2,
          textAlign: 'left',
          color: 'white'
        }}
      >
        {Object.keys(CATEGORIES).map((category) => (
          <ToggleButton value={category} aria-label={category} key={category}>
            <img
              src={CATEGORIES[category].icon}
              alt={category}
              // width="32" height="32"
              style={{
                width: "32px",
                height: "32px",
                margin: "-8px"
              }}
            />
          </ToggleButton>
        ))}
      </ToggleButtonGroup>
    </div>
  );

  return [button, filters];
}

function SearchBar(nodes, onGoToNode) {
  const [search, setSearch] = useState("");

  // Get the node names
  let node_names = nodes.map(node => node.data.node.friendlyName);

  const search_bar = (
    <Stack
      direction="row" spacing={1} justifyContent="center" alignItems="right"
      sx={{ bgcolor: 'grey.200', borderColor: 'grey.600' }}
    >
      <Autocomplete
        id="search"
        freeSolo
        options={node_names}
        renderInput={(params) => (
          <TextField
            {...params}
            label="Search"
            variant="filled"
            size="small"
            InputProps={{ ...params.InputProps, type: 'search' }}
          />
        )}
        onChange={(event, newValue) => {
          setSearch(newValue);
        }}
        sx={{ width: 250, marginRight: "-7px" }}
      />
      <Button variant="contained" onClick={() => { onGoToNode(search); }}>Go</Button>
    </Stack>
  );

  return [search_bar, search];
}

function FitView(reactFlowInstance) {
  // Fit the view in 10ms
  // It's a hack, but it works...
  setTimeout(() => {
    reactFlowInstance.fitView();
  }, 10);
}

const useRefresh = () => {
  // Visions of hack
  const state = useGraphState();
  const [refresh, setRefresh] = useState(0);
  const refreshPage = () => {
    setRefresh(refresh + 1);
    // Recompute layout
    GetLayout(state, nodes, edges);
    setRefresh(refresh + 1);
  };
  return refreshPage;
};

function Flow(props) {
  const state = useGraphState();
  const reactFlowInstance = useReactFlow();
  console.log("State:", state);

  // const [selected, setSelected] = useState(null);
  // const [selectedNode, onSelectNode] = useSelectedNode();
  let selectedNode = state.selected;
  const onSelectNode = (node) => {
    // state.selected = node;
    state.setSelected(node);
    // refreshPage();
  };

  // Filters
  const [filterButtons, filters] = useFilterButtons(
    state,
    // Fit the view when the filters change
    () => { FitView(reactFlowInstance); },
    // When we reset, we want to re-fit the view, and set selected and filters to blank
    (setFilters) => {
      FitView(reactFlowInstance);
      onSelectNode(null);
      setFilters({ ...filters, category: [] });
    }
  );

  // Search bar
  const [searchBar, search] = SearchBar(props.nodes, (search) => {
    // Find the node
    let node = props.nodes.find(node => node.data.node.friendlyName === search);
    if (node) {
      // Set the selected node
      onSelectNode(node.id);
      // Fit the view
      FitView(reactFlowInstance);
    }
  });

  let nodeTypes = useMemo(() => {
    return {
      tech: CreateReactFlowNode(state),
    };
  }, [state]);

  let nodes = null;
  if (selectedNode == null) {
    nodes = ApplyFilters(props.nodes, filters);
  }
  else {
    let to_show_ids = FindChain(selectedNode, props.edges);
    nodes = props.nodes.filter(node => to_show_ids.has(node.id));
  }

  // Find layout for nodes
  console.log("Finding layout for state:", state);
  GetLayout(state, nodes, props.edges);

  let edges = props.edges.map(edge => {
    return {
      ...edge,
      ...GetEdgeOptions(nodes, edge),
    };
  });

  // console.log(edges);

  return (
    <>
      <div style={{ zIndex: 1000, position: 'absolute', top: 0, left: 0, padding: 10 }}>
        {filterButtons}
      </div>
      <div style={{ zIndex: 1000, position: 'absolute', top: 2, right: 2 }}>
        {searchBar}
      </div>
      <ReactFlow
        // defaultNodes={props.nodes}
        nodes={nodes}
        edges={edges}
        // defaultEdgeOptions={edgeOptions}
        fitView
        style={{
          backgroundColor: '#D3D2E5',
        }}
        // connectionLineStyle={connectionLineStyle}
        nodesDraggable={false}
        nodesConnectable={false}
        edgesConnectable={false}
        // nodesSelectable={true}
        nodeTypes={nodeTypes}
        onNodeClick={(event, node) => {
          console.log("Clicked", node);
          onSelectNode(node.id);
          FitView(reactFlowInstance);
        }}
        maxZoom={1}
        minZoom={0.1}
      >
        <Controls />
        {/* <Background variant="dots" gap={12} size={1} /> */}
        <MiniMap />
      </ReactFlow>
    </>
  );
}

function App() {
  return (
    <div style={{ width: '100vw', height: '100vh' }}>
      <ReactFlowProvider>
        <Flow
          nodes={tech_nodes}
          edges={converted_edges}
          fitView
        >
          <Controls />
          <MiniMap />
        </Flow>
      </ReactFlowProvider>
    </div>
  );
}

export default App;
